/* override Button */

.btn:focus {
  outline: none !important;
}

/* override Panel */

.panel {
  margin: 0 5px 5px;
}

.panel-heading {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.panel-body {
  padding: 5px;
}

/* override Modal */

.modal {
  display: flex !important;
}

.modal-dialog {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  overflow: auto;
  margin: auto;
  max-height: 90%;
  max-width: 90%;
}

.modal-content {
  display: flex;
  flex-direction: column;
}

.modal-body {
  overflow-x: hidden;
  overflow-y: scroll;
  padding: 10px;
  text-align: center;
}

.modal-footer {
  text-align: center;
}

/* custom */

.app {
  text-align: center;
}

#bg {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-position: center center;
  background-size: cover;
  transform: translateZ(0);
  will-change: transform;
}

.banner {
  align-items: center;
  background-color: #eec901;
  background-repeat: no-repeat;
  background-size: auto 100%;
  display: flex;
  height: 50px;
  justify-content: flex-end;
  margin-bottom: 5px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

.banner .btn {
  background: none;
  border: none;
  color: black;
  font-size: 30px;
  line-height: 30px;
  height: 30px;
  margin-right: 5px;
  padding: 0;
  width: 30px;
}

.banner .btn .glyphicon {
  position: static;
}

.banner-placeholder {
  height: 55px;
}

.select-number {
  background-color: rgba(0,0,0,0.5);
  border: 2px solid #58ff64;
  border-radius: 3px;
  color: white;
  padding: 0 3px;
  margin: 10px 5px 0;
  text-align: left;
}

.select-number .check {
  height: 20px;
  margin-left: -10px;
  margin-top: -7px;
}

.nameko-panel {
  background: none;
  border: none;
}

.shadow,
.shadow img {
  width: 40px;
}

@media(min-width:768px) {
.shadow {
  margin-left: 30px;
}}

.shadow img:first-child {
  margin-bottom: -15px;
  margin-top: -10px;
  position: relative;
  z-index: 1;
}

.shadow img:last-child {
  margin-bottom: -10px;
}

.deco-wrapper {
  margin: 0 10px;
  position: relative;
}

.deco-wrapper.clickable {
  cursor: pointer;
}

.deco-wrapper.clickable:hover {
  opacity: 0.9;
}

.deco {
  position: absolute;
  z-index: 1;
}

.deco1-1 {
  left: 30px;
  top: 5px;
  width: 20px;
}

.deco1-2 {
  left: 5px;
  top: 20px;
  width: 20px;
}

.deco1-3 {
  left: -5px;
  bottom: -5px;
  width: 35px;
}

.deco1-4 {
  right: 0;
  top: -5px;
  width: 35px;
}

.deco1-5 {
  right: 35px;
  width: 60px;
  bottom: 18px;
}

.deco1-6 {
  right: 20px;
  width: 50px;
  bottom: 18px;
}

.deco2-1 {
  background-color: rgba(0,0,0,0.5);
  border-radius: .25em;
  color: white;
  font-size: 14px;
  padding: .2em .2em .3em;
  right: 35px;
  top: 30px;
}
@media(min-width:768px) {
.deco2-1 {
  font-size: 16px;
  top: 28px;
}}

.deco2-2 {
  left: -10px;
  width: 60px;
  top: -15px;
}

.deco2-3 {
  left: -5px;
  width: 70px;
  bottom: -10px;
}

.deco2-4 {
  left: -10px;
  width: 70px;
  top: 5px;
  transform: rotate(-15deg);
}

/* Tunnel Modal & Nameko Modal */

.hint-modal .modal-body,
.tunnel-modal .modal-body,
.nameko-modal .modal-body {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.hint-modal .modal-header,
.tunnel-modal .modal-header,
.nameko-modal .modal-header {
  background: linear-gradient(#5a391e, black);
  border: none;
  padding: 5px 15px;
}

.hint-modal .header-inner,
.tunnel-modal .header-inner,
.nameko-modal .header-inner {
  background: linear-gradient(#808834, black);
  border-radius: 20px;
  color: white;
  display: flex;
  padding: 5px;
}

.tunnel-modal .header-inner .text,
.nameko-modal .header-inner .text {
  line-height: 22px;
}

.hint-modal .modal-footer,
.tunnel-modal .modal-footer,
.nameko-modal .modal-footer {
  background: linear-gradient(#5a391e, black);
  border: none;
  padding: 15px;
}

.hint-modal .modal-footer .btn,
.tunnel-modal .modal-footer .btn,
.nameko-modal .modal-footer .btn {
  background: none;
  border-color: #f9f1d9;
  color: #f9f1d9;
}

/* Tunnel & Tunnel Modal */

.tunnel-modal .header-inner .text {
  padding-left: 10px;
}

.tunnel-modal .modal-body {
  border-top: 10px solid transparent;
}

.tunnel-modal .text {
  width: calc(100% - 80px);
}

.tunnel-modal .img,
.tunnel-modal .img img {
  width: 80px;
}

.tunnel-modal .img img {
  position: relative;
  bottom: -10px;
}

.tunnel-banner,
.tunnel {
  border: none;
}

.select-team-panel,
.tunnel-banner {
  background: linear-gradient(#5a391e, black);
  border: none;
  border-radius: 20px;
  padding: 15px;
}

.tunnel {
  background: none;
  margin: 10px 0 10px 10%;
}
.tunnel:last-child {
  margin-bottom: 0;
}
@media(min-width:768px) {
.tunnel {
  margin-left: 20%;
}
}

.tunnel-banner .panel-heading {
  background-size: cover;
  background-position: center;
  border: none;
  height: 50px;
}

.tunnel .panel-heading {
  background: linear-gradient(#dc6d27, black);
}

.tunnel-banner .panel-heading .spacer {
  flex: 1;
}

.tunnel-banner.selectable > .panel-heading:hover,
.tunnel > .panel-heading:hover {
  cursor: pointer;
}

.tunnel .panel-body {
  padding: 0;
}

.tunnel-banner .label,
.tunnel .label {
  background-color: rgba(0,0,0,0.5);
  font-size: 14px;
  font-weight: normal;
  overflow: auto;
  padding: .2em .2em .3em;
  text-overflow: ellipsis;
}
@media(min-width:768px) {
.tunnel-banner .label,
.tunnel .label {
  font-size: 16px;
}
}

.tunnel-banner .check-block,
.tunnel .check-block {
  height: 28px;
  line-height: 26px;
  margin-right: 10px;
  padding: 0;
  width: 28px;
}
@media(min-width:768px) {
.tunnel-banner .check-block,
.tunnel .check-block {
  margin-right: 10px;
}}

.tunnel .check-block:hover {
  background-color: rgba(0,0,0,0.2);
}

.tunnel-banner .check,
.tunnel .check {
  max-height: 28px;
  max-width: 28px;
}

.tunnel .omamori {
  width: 50px;
}

.tunnel-banner > * > .panel-body {
  padding: 0;
}

.memo-wrapper {
  padding: 5px;
}

.memo {
  background-color: rgba(249,241,217,.7);
  border-radius: 4px;
  color: #5a391e;
  padding: 2px;
}

.memo .reward {
  background-size: 27px;
  background-position: 1px center;
  padding: 0 2px;
}

.memo .reward img {
  height: 36px;
  width: 36px;
  margin: -6px;
}

/* Nameko */

.select-team-panel .panel-heading {
  background: #808834;
  border: none;
  border-radius: 20px;
  height: 54px;
  justify-content: flex-start;
  padding-left: 25px;
}

.all-block {
  margin-left: -5px;
  margin-right: -5px;
}

.simple-nameko {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
}

.simple-nameko .img {
  max-height: 40px;
  max-width: 100%;
}

.nameko .check {
  left: 2px;
  position: absolute;
  top: 4px;
  height: 20px;
}

.nameko .name,
.simple-nameko .name {
  background-color: rgba(0,0,0,0.5);
  border-radius: 3px;
  color: white;
  font-size: 12px;
  padding: 2px 5px;
  position: relative;
}

.nameko-wrapper {
  padding: 0;
}

.nameko {
  background-color: rgba(249,241,217,0.7);
  border-radius: 4px;
  border-color: #ccc;
  border: 1px solid #ccc;
  margin: 2px;
  padding: 2px;
}

.nameko .name {
  padding: 0 3px;
  border: 2px solid transparent;
}

.nameko.selected .name {
  border-color: #58ff64;
}

.nameko.clickable:active,
.nameko.clickable:focus,
.nameko.clickable:hover {
  background-color: rgba(249,241,217,1);
  cursor: pointer;
}

.nameko .simple-nameko {
  flex: 1;
  flex-direction: column-reverse;
  justify-content: center;
}

.top-ctn {
  display: flex;
  margin-top: 2px;
}

.skill-ctn {
  height: 22px;
  line-height: 22px;
  margin: 0 -4px;
}
@media(min-width:768px) {
  .skill-ctn {
    height: 30px;
    line-height: 30px;
  }
}

.skill {
  background-color: transparent;
  background-position: center;
  background-size: cover;
  border: none;
  display: inline-block;
  vertical-align: middle;
  height: 18px;
  width: 18px;
}
@media(min-width:768px) {
  .skill {
    height: 24px;
    margin: 0 2px;
    width: 24px;
  }
}

.skill.unselected {
  filter: grayscale(100%);
  opacity: 0.5;
}

.value-ctn {
  display: flex;
  flex-direction: column;
  width: 40px;
}
@media(min-width:768px) {
  .value-ctn {
    line-height: 24px;
    width: 60px;
  }
}

.value {
  background-position: 0 center;
  background-repeat: no-repeat;
  background-size: 11px;
  margin-left: 1px;
  padding: 1px 1px 1px 12px;
}
@media(min-width:768px) {
  .nameko .value {
    background-size: 22px;
    font-size: 16px;
    margin: 0 1px 0 2px;
    padding: 2px 2px 2px 24px;
  }
}

.value-area {
  display: flex;
  justify-content: center;
  text-align: center;
}

.value-area > * {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}

.value-area .btn {
  background-color: lightgray;
  border-width: 0;
  border-radius: 50%;
  height: 44px;
  padding: 0;
  width: 44px;
}

.value-area .value {
  background-size: 22px;
  font-size: 16px;
  margin: 5px auto;
  padding: 2px 2px 2px 24px;
}

.nameko .btn {
  background: none;
  border: 1px solid #5a391e;
  color: #5a391e;
  margin: 3px 0;
  padding: 3px 6px;
}
.nameko .btn:hover {
  background: rgba(0,0,0,0.2);
}
@media(min-width:768px) {
  .nameko .btn {
    padding: 6px 12px;
  }
}

.nameko .btn .glyphicon {
  margin-right: 3px;
}

/* Nameko Modal */

.nameko-modal .header-inner .shadow {
  margin-right: 5px;
}

.nameko-modal .modal-body {
  padding: 0;
}

.nameko-modal .content {
  padding: 10px;
  width: 100%;
}

.nameko-modal .buttons {
  display: flex;
  padding-bottom: 10px;
}

.nameko-modal .buttons .btn {
  background: none;
  border-color: #f9f1d9;
  color: #f9f1d9;
}

.nameko-modal .editor {
  align-items: center;
  background-color: rgba(255,255,255,0.5);
  flex-direction: column;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 70px;
  bottom: 65px;
  right: 0;
}

.nameko-modal .editor .simple-nameko .img {
  max-height: 80px;
}

.nameko-modal .editor .simple-nameko .name {
  font-size: 16px;
}

.nameko-modal .editor .btn {
  box-shadow: 1px 1px 2px 2px rgba(0,0,0,0.3);
}

.nameko-modal .editor .skill-area .btn {
  background: none;
  border: none;
  padding: 0;
  margin: 6px 12px;
}

.nameko-modal .editor .skill {
  width: 30px;
  height: 30px;
}
@media(min-width:768px) {
.nameko-modal .editor .skill {
  width: 48px;
  height: 48px;
}
}

.nameko-modal .editor .notice {
  margin-top: 10px;
}

/* Hint Modal */

.hint-modal .modal-body {
  color: white;
  text-align: left;
}

.hint-modal .modal-body > *:not(:last-child) {
  margin-bottom: 20px;
}

.hint-modal .modal-body img {
  margin-left: 20px;
  max-width: calc(100% - 20px);
}

/* Result Panel */

.success,
.fail {
  height: 36px;
  width: 36px;
}

.reward {
  background-size: 36px;
  background-position: 2px center;
  background-repeat: no-repeat;
  display: inline-block;
  padding: 0 2px;
}

.reward img {
  height: 54px;
  width: 54px;
  margin: -9px;
}

.reward span.treasure {
  color: blue;
}

.reward span.item {
  color: #f44336;
}

.result-panel {
  background: none;
  text-align: left;
}

.result-panel .panel {
  padding: 2px;
}

.result-panel h1,
.result-group {
  position: sticky;
  margin: 0;
}

.result-panel h1 {
  color: #f9f1d9;
  background-color: #5a391e;
  font-size: 18px;
  line-height: 30px;
  overflow: hidden;
  padding: 3px 5px;
  text-overflow: ellipsis;
  top: 50px;
  white-space: nowrap;
  z-index: 2;
}

.result-group {
  background-color: #f9f1d9;
  border: 5px solid #5a391e;
  color: #5a391e;
  top: 86px;
  z-index: 1;
  margin-bottom: 5px;
  padding-left: 3px;
}

.result.panel {
  background-color: rgba(249, 241, 217, 0.7);
}

.result .fail {
  margin-right: -18px;
}

.result .namekos {
  padding-bottom: 2px;
}

.result .namekos > * {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
}

.result .simple-nameko {
  margin: 0 2px;
}

.result .skill {
  height: 36px;
  width: 36px;
}

.footer-placeholder {
  height: 69px;
}

.to-top {
  position: relative;
}

.to-top > * {
  display: block;
}

.to-top img:first-child {
  left: -16px;
  top: 5px;
  position: absolute;
  width: 60px;
  z-index: -1;
}

.to-top img:nth-child(2) {
  max-height: 40px;
}

/* Run */

.footer {
  bottom: 20px;
  left: 50%;
  position: fixed;
  transform: translate(-50%,0);
  width: fit-content;
  z-index: 100;
}

.footer .btn {
  font-size: 14px;
  font-weight: bold;
  height: 60px;
  position: relative;
}
@media(min-width:768px) {
.footer .btn {
  font-size: 16px;
}
}

#sort .glyphicon {
  margin-left: 6px;
}

#sort .caret {
  display: none;
}

#sort,
#run {
  background: none;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border: none;
}

#sort {
  margin-left: 5px;
  margin-right: -15px;
  padding-right: 20px;
}
@media(min-width:768px) {
#sort {
  margin-right: 0;
}}

#run {
  margin-left: 30px;
  padding-right: 15px;
}

#sort img:first-child,
#run img:first-child {
  height: 60px;
  position: absolute;
}

#sort img:first-child {
  left: -25px;
  top: -20px;
}

#run img:first-child {
  left: -30px;
  top: -20px;
}

#sort img:nth-child(2),
#run img:nth-child(2) {
  height: 70px;
  position: relative;
}

#sort img:nth-child(2) {
  bottom: 3px;
  margin: -20px 0 0 -15px;
}

#run img:nth-child(2) {
  margin: -20px -25px 0 -45px;
}
